
.Avb-Posts {
    padding: 0px 13rem;
    background-color: white;
    width: 100%;
    overflow-x: hidden;
}

@media(max-width:768px), (min-width: 0px) {
    .Avb-Posts {
        padding: 0px ;
        width: 100%;
    }
    .Avb-Posts-mainHeading {
        text-align: center;
        margin: 30px 0px;
    }
}

.Avb-Posts-navbar {
    padding: 0px 80px;
}

.Navbar {
    padding: 30px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.Navbar div:nth-child(1) {
    font-size: 30px;
    font-weight: 900;
    cursor: pointer;
}
ul {
    list-style: none;
}
.centerNavbar ul {
    display: flex;
    gap: 50px;
    font-size: 14px;
    font-weight: 700;
}

.centerNavbar ul li:hover {
    color: var(--bg-caroline-blue);
    cursor: pointer;
    transition: color 0.2s;
}

.rightNabar {
    height: 6rem;
    width: 6rem;
    border-bottom-left-radius: 50%;
    margin-top: -30px;
    margin-right: -40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.rightNabar {
    cursor: pointer;
}
.rightNabar ion-icon {
    font-size: 40px;
}


.Avb-Posts-popularposts{
    display: flex;
    justify-content: space-around;
}
.allposts{
    width: 18rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    color: white;
    border: 2px solid white;
    padding: 10px;
    border-radius: 9px;
    gap: 10px;
    cursor: pointer;
    transition: transform 0.2s;
    position: relative;
    color: black;
    border: 0.2px solid rgb(199, 197, 197);
}

.allposts:hover{
    box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    -webkit-box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    -moz-box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    transform: translateY(-10px);

}

.text-black {
    color: black;
}
.text-black:hover {
    color: var(--bg-caroline-blue);
}
/* change class name */
.allposts-img img{
    width: 100%;
    height: 10rem;
}

.allposts-title {
    font-weight: 800;
    font-size: 25px;
}

.allposts-description {
    font-size: 14px;
}

.allposts-upload-date {
    font-size: 13px;
    display: flex;
    width: 100%;
}


/* main coding */

.Avb-Posts-mainHeading {
    font-size: 40px ;
    font-weight: 900;
    margin: 30px auto;
    
}

.Avb-Posts-landing-img {
    text-align: center;
}
.Avb-Posts-landing-img img {
    width: 60%;
    margin: 20px auto;
}

.Avb-Posts-paragraph {
    font-size: 23px;
    margin: 50px 200px;
}

.Avb-Posts-paragraph::first-letter {
    font-size: 30px;
    font-weight: 800;
}


.code {
    background-color: var(--bg-oxford-blue);
    color: white;
    width: fit-content;
    margin: auto;
    padding:  10px 100px 10px 20px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    font-size: 17px;
    font-weight: 800;
}


.Avb-Posts-popularposts {
    margin: 40px 30px;
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
}

.responsive-centerNavbar {
    display: none;
}

#menu-btn {
    display: none;
}

/* Responsive design */

@media (min-width:0px) and (max-width:800px) {

    .Navbar {
        padding: 20px 20px;
    }
    .Navbar div:nth-child(1) {
        font-size: 28px;
    }
    .rightNabar {
        background-color: transparent;
        height: 0px;
    }

    .rightNabar ion-icon {
        font-size: 25px;
        margin-top: 30px;
        margin-right: 40px;
    }

    .centerNavbar {
        display: none;
    }

    .responsive-centerNavbar {
        display: flex;
        position: absolute;
        width: 100%;
        justify-content: center;
        backdrop-filter: blur(20px);
        top: 0;
        right: 0;
        height: 100vh;
        z-index: 1;
        align-items: center;
        transform: translateX(100%);
        transition: transform 0.3s;
    }

    .responsive-centerNavbar ul {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 90px;
        align-items: center;
        justify-content: center;
        font-weight: 900;
        font-size: 30px;
        color: black;
    }

    .responsive-centerNavbar ul li:hover {
        color: var(--bg-caroline-blue);
    }

    #menu-btn {
        display: flex;
        font-size: 30px;
        z-index: 5;
        right: 30px;
    }
}

@media (min-width: 0px) and (max-width: 1200px) {
    .Avb-Posts-navbar {
        padding: 0px;
    }

    .Avb-Posts-landing-img img {
        width: 90%;
        height: 35vh;
    }
    .Avb-Posts-paragraph {
        margin: 50px 20px;
    }

    .code {
        margin: 20px;
    }


}

.no-first-captial::first-letter {
    font-size: 20px;
    font-weight: 500;
}

.Avb-middle-images {
    text-align: center;
}

@media (min-width: 0px) and (max-width:1000px) {
    .Avb-middle-images img {
        width: 90%;
    }
}