body, html {
    overflow-x: hidden;
}

.Navbar {
    padding: 30px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.Navbar div:nth-child(1) {
    font-size: 30px;
    color: white;
    font-weight: 900;
    cursor: pointer;
}

ul {
    list-style: none;
    color: white;
}

.centerNavbar ul {
    display: flex;
    gap: 50px;
    font-size: 14px;
    font-weight: 700;
}

.text-white {
    color: white;
}

.text-white:hover {
    color: var(--bg-caroline-blue);
    cursor: pointer;
    transition: color 0.2s;
}


.rightNabar {
    background-color: white;
    height: 6rem;
    width: 6rem;
    border-bottom-left-radius: 50%;
    margin-top: -30px;
    margin-right: -40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.rightNabar {
    cursor: pointer;
}

.rightNabar ion-icon {
    font-size: 40px;
    color: black;
}

.responsivee-centerNavbar {
    display: none;
}

#menu-btn {
    display: none;
}



/* Responsive design */
@media (min-width:0px) and (max-width:800px) {

    .Navbar {
        padding: 20px 20px;
        display: flex;
        align-items: center;
    }
    .Navbar div:nth-child(1) {
        font-size: 28px;
    }
    .rightNabar {
        background-color: transparent;
        height: 0px;
    }

    .rightNabar ion-icon {
        color: white;
        font-size: 25px;
        margin-top: 30px;
        margin-right: 40px;
    }

    .centerNavbar {
        display: none;
    }

    .responsivee-centerNavbar {
        display: flex;
        position: absolute;
        width: 100%;
        justify-content: center;
        backdrop-filter: blur(20px);
        top: 0;
        right: 0;
        height: 100vh;
        z-index: 1;
        align-items: center;
        transform: translateX(100%);
        transition: transform 0.3s;
    }

    .responsivee-centerNavbar ul {
        color: white;
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 90px;
        align-items: center;
        justify-content: center;
        font-weight: 900;
        font-size: 30px;
    }

    .responsivee-centerNavbar ul li:hover {
        color: var(--bg-caroline-blue);
    }

    #menu-btn {
        display: flex;
        color: white;
        font-size: 30px;
        z-index: 5;
        right: 30px;
    }
}