.Footer {
    position: relative;
    background-color: var(--bg-oxford-blue);
    color: white;
    display: grid;
    grid-template-areas: 'footerLogo fls frs';
    justify-content: space-evenly;
    padding: 50px 0px;
}

.footer-myLogo {
    grid-area: footerLogo;
    font-size: 40px;
    font-weight: 900;
    display: flex;
    align-items: center;
    margin-left: -100px;
}
.footer-leftSide {
    grid-area: fls;
    width: 13rem;
    display: flex;
    flex-direction: column;
    gap: 30px;
    text-align: center;
}

.footer-about-heading, .footer-location-heading {
    font-weight: 900;
    font-size: 35px;
    margin-bottom: 10px;
}

.footer-about-page {
    font-size: 15px;
}

.footer-loactaion {
    display: flex;
    align-items: center;
    gap: 5px;
    justify-content: center;
}



.footer-rightSide {
    grid-area: frs;
}

.footer-socialMedialinkheading {
    font-weight: 900;
    font-size: 30px;
    margin-bottom: 40px;
}

.footer-links {
    display: flex;
    justify-content: center;
    gap: 30px;
}

#insta-logo, #git-logo, #link-logo {
    font-size: 50px;
    cursor: pointer;
    transition: color 0.2s;
}

#insta-logo:hover, #git-logo:hover, #link-logo:hover {
    color: rgb(179, 177, 176);
}

.footer-hori-line {
    display: none;
}
/* Responsive Design */
@media (min-width: 0px) and (max-width: 1200px) {
    .Footer {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 30px;
    }

    .footer-myLogo {
        margin: 0px;
    }

    .footer-hori-line {
        display: block;
        width: 90%;
    }
}