* {
    padding: 0px;
    margin: 0px;
    text-decoration: none;
}

:root {
    --bg-wild-blue-yender: hsla(216, 33%, 68%, 1);
    --bg-caroline-blue: hsla(199, 89%, 49%, 1);
    --bg-oxford-blue: hsla(222, 47%, 11%, 1);
    --bg-oxford-blueee: rgb(8, 12, 24);
    --bg-oxford-blueff: rgb(74, 150, 212);
}

body {
    background-color: rgb(38, 57, 99); 
}

body, html {
    overflow-x: hidden;
}


.blueball1 {
    background: linear-gradient(var(--bg-wild-blue-yender), var(--bg-caroline-blue));
    height: 30rem;
    width: 30rem;
    position: fixed;
    top: 100px;
    left: 200px;
    filter: blur(200px);
}
.blueball2 {
    background: linear-gradient(var(--bg-wild-blue-yender), var(--bg-caroline-blue));
    height: 30rem;
    width: 30rem;
    position: fixed;
    top: 200px;
    right: 100px;
    filter: blur(200px);
}

.container {
    width: 75%;
    margin: auto;
    padding-bottom: 1px;
    background-color: var(--bg-oxford-blue);
}

@media (min-width:0px) and (max-width:1024px){
    .container {
        width: 100%;
    }
}

.active {
    color: var(--bg-caroline-blue);
    border-bottom: 2px solid var(--bg-caroline-blue);
}