
.AllPosts {
    padding: 0px 50px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.allPosts-search-box {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
    box-shadow: 0px 0px 52px 14px rgba(0, 0, 0, 0.46);
    -webkit-box-shadow: 0px 0px 52px 14px rgba(0, 0, 0, 0.46);
    -moz-box-shadow: 0px 0px 52px 14px rgba(0, 0, 0, 0.46);
    border-radius: 4px;
}

.allposts-search-field {
    padding: 12px 14px;
    background-color: transparent;
    color: black;
    outline: none;
    border: none;
    border-top-left-radius: 4px;
    font-weight: 700;
    width: 100%;
    

}

.allposts-searchbtn {
    padding: 12px 10px;
    border: none;
    background: linear-gradient(to left, var(--bg-oxford-blueff), var(--bg-caroline-blue));
    color: white;
    font-weight: 900;

}

.allposts-searchbtn:hover {
    background: linear-gradient(to right, var(--bg-oxford-blueff), var(--bg-caroline-blue));

}

.allposts-posts {
    margin: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 50px;
}

.allposts {
    width: 18rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    color: white;
    border: 2px solid white;
    padding: 10px;
    border-radius: 9px;
    gap: 10px;
    cursor: pointer;
    transition: transform 0.2s;
    position: relative;
    color: black;
    border: 0.2px solid rgb(199, 197, 197);
}

.allposts:hover {
    box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    -webkit-box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    -moz-box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    transform: translateY(-10px);

}

/* change class name */
.allposts-img img{
    width: 100%;
    height: 10rem;
}

.allposts-title {
    font-weight: 800;
    font-size: 25px;
}

.allposts-description {
    font-size: 14px;
}

.allposts-upload-date {
    font-size: 13px;
    display: flex;
    width: 100%;
}

/* Responsive design */
@media (min-width: 0px) and (max-width: 550px) {
    .AllPosts {
        padding: 0px 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .allposts-posts {
        margin: 0px;
        display: flex;
        flex-direction: column;
        gap: 40px;
        margin-top: 20px;
        align-items: center;
    }

    
}


.post-tags {
    position: absolute;
    color: transparent;
}