.FirstSection {
    position: relative;
    margin: 12rem 35px;
    display: grid;
    grid-template-areas: 'fs-info robot-img' 'fs-about robot-img' 'fs-email robot-img';
    gap: 17px;
}

.FirstSection-MyName {
    font-size: 38px;
    font-weight: 900;
    color: white;
    grid-area: fs-info;

}

.text-blue {
    color: var(--bg-oxford-blue);
}

.FirstSection-about {
    width: 30rem;
    color: white;
    font-weight: 600;
    grid-area: fs-about;
}

.FirstSection-EmailField-element {
    margin: 30px 20px;
    border-bottom-left-radius: 4px;
    width: fit-content;
    box-shadow: 0px 0px 52px 14px rgba(0, 0, 0, 0.46);
    -webkit-box-shadow: 0px 0px 52px 14px rgba(0, 0, 0, 0.46);
    -moz-box-shadow: 0px 0px 52px 14px rgba(0, 0, 0, 0.46);
    grid-area: fs-email;
}

.FirstSection-email-input-field {
    padding: 12px 14px;
    background-color: transparent;
    color: white;
    outline: none;
    border: none;
    border-top-left-radius: 4px;
    font-weight: 700;
    
}

.FirstSection-email-input-field::placeholder {
    color: white;
}

.FirstSection-EmailField-element button {
    padding: 12px 10px;
    border: none;
    background: linear-gradient(to left, var(--bg-oxford-blueff), var(--bg-caroline-blue));
    color: white;
    font-weight: 900;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.FirstSection-EmailField-element button:hover {
    background: linear-gradient(to right, var(--bg-oxford-blueff), var(--bg-caroline-blue));

}

/* Responsive design */
@media (min-width: 0px) and (max-width: 800px) {
    .FirstSection {
        margin-top: 6rem ;
    }
    .FirstSection-about {
        width: 100%;
    }

    .FirstSection-EmailField-element {
        display: flex;
    }

    .FirstSection-email-input-field {
        width: 100%;
    }

    
}

.FirstSection-img {
    grid-area: robot-img;
    position: absolute;
    margin: -8rem ;
} 

.FirstSection-img img {
    width: 25rem;
}

@media (min-width: 0px) and (max-width: 1400px) {
    .FirstSection-img {
        display: none;
    }
}