.Contact-page {
    background-image: url('../images/coding-background.jpeg');
    background-size:  100% ;
    height: 100vh;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact-page-navbar {
    background: rgba(0, 0, 0, 0.8);
    position: absolute;
    top: 0px;
    right: 0px;
    left: 0px;
    padding: 0px 100px;
}

.contact-page-box {
    color: white;
    backdrop-filter: blur(5px);
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.8);
}

.contact-page-heading {
    font-size: 35px;
    font-weight: 900;
}

.contact-way {
    font-size: 17px;
    font-weight: 700;
}

.contact-instagram {
    cursor: pointer;
}

.contact-instagram:hover {
    text-decoration: underline;
}