.LatestPosts, .PopularPosts {
    margin: 30px;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: space-around;
    gap: 30px;
}

hr {
    width: 95%;
    margin: auto;
}

.LatestPosts-headline, .PopularPosts-headline {
    color: white;
    margin: 30px;
    font-size: 33px;
    font-weight: 900;
    position: relative;
}

.PopularPosts-headline {
    margin: 40px 30px;
}

.latestposts, .popularposts {
    width: 18rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    color: white;
    border: 2px solid white;
    padding: 10px;
    border-radius: 9px;
    gap: 10px;
    cursor: pointer;
    transition: transform 0.2s;
    position: relative;
    color: black;
}

.latestposts:hover, .popularposts:hover{
    box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    -webkit-box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    -moz-box-shadow: 0px 0px 49px 5px rgba(0, 0, 0, 0.61);
    transform: translateY(-10px);

}

/* change class name */
.latestposts-img, .popularposts-img {
    width: 100%;
    height: 10rem;
    background-color: gray;
}

.latestposts-title, .popularposts-title {
    font-weight: 800;
    font-size: 25px;
}

.latestposts-description, .popularposts-description {
    font-size: 14px;
}

.latestposts-upload-date, .popularposts-upload-date {
    font-size: 13px;
    display: flex;
    width: 100%;
}

.moreOptions {
    margin: auto;
    position: relative;
    display: flex;
    align-items: center;
    margin: 30px;
    justify-content: center;
    color: white;
    cursor: pointer;
}
.moreOptions:hover {
    text-decoration: underline;
}